
"use client";

import React, { useState, useEffect, useRef, useCallback } from 'react';
import Image from 'next/image';
import { type ProductWithImage } from '../page';
import { Button } from '@/components/ui/button';

type ProductCarouselProps = {
    products: ProductWithImage[];
    isShowingDetail: boolean;
    setIsShowingDetail: (isShowing: boolean) => void;
    setActiveProduct: (product: ProductWithImage) => void;
}

export default function ProductCarousel({ products, isShowingDetail, setIsShowingDetail, setActiveProduct }: ProductCarouselProps) {
    const [carouselClass, setCarouselClass] = useState('');
    const listRef = useRef<HTMLDivElement>(null);
    const carouselRef = useRef<HTMLDivElement>(null);
    const unacceptClickTimeout = useRef<NodeJS.Timeout | null>(null);
    const autoSlideInterval = useRef<NodeJS.Timeout | null>(null);

    const showSlider = useCallback((type: 'next' | 'prev') => {
        if (carouselRef.current) {
            carouselRef.current.classList.remove('next', 'prev');
        }
        
        const list = listRef.current;
        if (list) {
            const items = list.querySelectorAll('.item');
            if (type === 'next') {
                list.appendChild(items[0]);
                carouselRef.current?.classList.add('next');
            } else {
                list.prepend(items[items.length - 1]);
                carouselRef.current?.classList.add('prev');
            }
            
            // Update active product
            const activeItemElement = list.children[1] as HTMLElement;
            if(activeItemElement) {
                const activeId = activeItemElement.dataset.id;
                const newActiveProduct = products.find(p => p.id === activeId);
                if (newActiveProduct) {
                    setActiveProduct(newActiveProduct);
                }
            }
        }
        
        if (unacceptClickTimeout.current) clearTimeout(unacceptClickTimeout.current);
        unacceptClickTimeout.current = setTimeout(() => {
            // No need to manage pointer events in React like this
        }, 2000);
    }, [products, setActiveProduct]);

    const stopAutoSlider = useCallback(() => {
        if (autoSlideInterval.current) {
            clearInterval(autoSlideInterval.current);
        }
    }, []);

    const startAutoSlider = useCallback(() => {
        stopAutoSlider();
        autoSlideInterval.current = setInterval(() => {
            showSlider('next');
        }, 6000);
    }, [stopAutoSlider, showSlider]);

    useEffect(() => {
        if (!isShowingDetail) {
            startAutoSlider();
        } else {
            stopAutoSlider();
        }
        return () => stopAutoSlider();
    }, [isShowingDetail, startAutoSlider, stopAutoSlider]);

    const handleSeeMoreClick = () => {
        setIsShowingDetail(true);
    };

    const handleBackClick = () => {
        setIsShowingDetail(false);
    };

    return (
        <div ref={carouselRef} className={`carousel ${isShowingDetail ? 'showDetail' : ''} ${carouselClass}`}>
            <div ref={listRef} className="list">
                {products.map((product) => (
                    <div className="item" key={product.id} data-id={product.id}>
                        {product.imageUrl && (
                             <Image 
                                src={product.imageUrl} 
                                alt={product.name} 
                                width={800} 
                                height={800}
                                className="object-contain"
                                data-ai-hint={product.imageHint}
                            />
                        )}
                        <div className="introduce">
                            <div className="title">Discover</div>
                            <div className="topic">{product.name}</div>
                            <div className="des">{product.description}</div>
                            <button className="seeMore" onClick={handleSeeMoreClick}>
                                SEE MORE &#8594;
                            </button>
                        </div>
                        <div className="detail">
                            <div className="title">{product.name}</div>
                            <div className="des">{product.description}</div>
                            <div className="specifications">
                                {product.category && <div><p>{product.category}</p><p>Category</p></div>}
                                {product.price && <div><p>{product.price}</p><p>Price</p></div>}
                            </div>
                            <div className="checkout">
                                <Button>ADD TO CART</Button>
                                <Button variant="outline">CHECKOUT</Button>
                            </div>
                        </div>
                    </div>
                ))}
            </div>
            <div className="arrows">
                <button id="prev" onClick={() => showSlider('prev')}>&lt;</button>
                <button id="next" onClick={() => showSlider('next')}>&gt;</button>
            </div>
            <button id="back" onClick={handleBackClick}>Back</button>
        </div>
    );
};
